import { Component, EventEmitter, forwardRef, Input, Output, ViewEncapsulation } from '@angular/core';
import {
  AbstractControl,
  FormsModule,
  NG_VALIDATORS,
  NG_VALUE_ACCESSOR,
  ValidationErrors,
  Validator
} from '@angular/forms';
import { MatSlideToggleChange, MatSlideToggleModule } from '@angular/material/slide-toggle';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { BooleanInput, coerceBooleanProperty } from '@angular/cdk/coercion';
import { NgClass } from '@angular/common';
import { A11yModule } from '@angular/cdk/a11y';

import { BaseControlValueAccessor } from 'sidsc-components/core';

type DscSwitchLabelPosition = 'before' | 'after';

type DscSwitchVariant = 'highlight' | 'accent' | 'danger';

type DscSwitchSize = 'large' | 'standard' | 'small';

@Component({
  selector: 'dsc-switch',
  standalone: true,
  imports: [MatSlideToggleModule, MatCheckboxModule, FormsModule, NgClass, A11yModule],
  templateUrl: './dsc-switch.component.html',
  styleUrls: ['./dsc-switch.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => DscSwitchComponent),
      multi: true
    },
    {
      provide: NG_VALIDATORS,
      useExisting: DscSwitchComponent,
      multi: true
    }
  ]
})
export class DscSwitchComponent extends BaseControlValueAccessor<BooleanInput> implements Validator {
  switchClasses: any;

  @Input() labelPosition: DscSwitchLabelPosition = 'after';

  @Input() name!: string;

  @Input() label?: string;

  @Input('aria-describedby') ariaDescribedby!: string;

  @Input('aria-label') ariaLabel!: string;

  @Input('aria-labelledby') ariaLabelledby!: string | null;

  @Input()
  override get disabled(): boolean {
    return super.disabled;
  }
  override set disabled(value: BooleanInput) {
    super.disabled = value;
  }

  @Input()
  get checked(): boolean {
    return this._checked;
  }
  set checked(value: BooleanInput) {
    this._checked = coerceBooleanProperty(value);
  }

  private _checked: boolean = false;

  @Input()
  get required(): boolean {
    return this._required;
  }
  set required(value: BooleanInput) {
    this._required = coerceBooleanProperty(value);
  }

  private _required: boolean = false;

  @Input()
  get size(): DscSwitchSize {
    return this._size;
  }

  set size(value: DscSwitchSize) {
    this._size = value;
    this.switchLabelSize = `mat-mdc-slide-toggle--${value}`;
  }

  private _size: DscSwitchSize = 'standard';

  protected switchLabelSize = 'mat-mdc-slide-toggle--standard';

  @Input()
  get variant(): DscSwitchVariant {
    return this._variant;
  }

  set variant(value: DscSwitchVariant) {
    this._variant = value;
    this.switchVariant = `mat-mdc-slide-toggle--${value}`;
  }

  private _variant: DscSwitchVariant = 'highlight';

  protected switchVariant = 'mat-mdc-slide-toggle--highlight';

  @Input()
  get hideIcon(): boolean {
    return this._hideIcon;
  }
  set hideIcon(value: BooleanInput) {
    this._hideIcon = coerceBooleanProperty(value);
  }

  private _hideIcon: boolean = false;

  @Output() readonly change: EventEmitter<any> = new EventEmitter<any>();

  @Output() readonly toggleChange: EventEmitter<void> = new EventEmitter<void>();
  toggle(): void {
    this.checked = !this.checked;
    this.val = this.checked;
  }

  override writeValue(value: BooleanInput): void {
    this.checked = !!value;
    this.val = this.checked;
  }

  onChanged($event: MatSlideToggleChange): void {
    this.change.emit($event);
  }

  onToggleChange(): void {
    this.toggleChange.emit();
  }

  validate(control: AbstractControl<any, any>): ValidationErrors | null {
    return control.value === null || control.value === undefined ? { required: true } : null;
  }
}
